/* eslint-disable jsx-a11y/accessible-emoji */
import React, { useState } from 'react';
import classNames from 'classnames';
import './App.scss';

import usersFromServer from './api/users';
import categoriesFromServer from './api/categories';
import productsFromServer from './api/products';

const products = productsFromServer.map((product) => {
  const category = categoriesFromServer.find(({ id }) => (
    id === product.categoryId
  ));
  const user = usersFromServer.find(({ id }) => (
    id === category.ownerId
  ));

  return {
    ...product,
    category,
    user,
  };
});

function getNormalizedString(string) {
  return string.toLowerCase().trim();
}

function getFilterProductByUser(
  allProducts,
  currentUser = '',
  { query },
) {
  let filteredProducts = allProducts;

  if (currentUser !== 'All') {
    filteredProducts = filteredProducts.filter(product => (
      product.user.name === currentUser.name
    ));
  }

  if (query) {
    const normalizedQuery = getNormalizedString(query);

    filteredProducts = filteredProducts.filter((product) => {
      const normalizedName = getNormalizedString(product.name);

      return normalizedName.includes(normalizedQuery);
    });
  }

  return filteredProducts;
}

export const App = () => {
  const [selectedUser, setSelectedUser] = useState('All');
  const [query, setQuery] = useState('');
  const [category, setCategory] = useState([]);

  const visibleProduct = getFilterProductByUser(
    products, selectedUser, { query },
  );

  const selectCategory = (currentCategory) => {
    setCategory([...category, currentCategory]);
  };

  // eslint-disable-next-line no-unused-vars
  const unselectCategory = (removeCategory) => {
    setCategory(category.filter(categ => (
      categ.id !== removeCategory.id
    )));
  };

  return (
    <div className="section">
      <div className="container">
        <h1 className="title">Product Categories</h1>

        <div className="block">
          <nav className="panel">
            <p className="panel-heading">Filters</p>

            <p className="panel-tabs has-text-weight-bold">
              <a
                data-cy="FilterAllUsers"
                href="#/"
                className={classNames({
                  'is-active': selectedUser === 'All',
                })}
                onClick={() => (
                  selectedUser !== 'All' && setSelectedUser('All'))}
              >
                All
              </a>

              {usersFromServer.map(user => (
                <a
                  key={user.id}
                  className={classNames({
                    'is-active': selectedUser.id === user.id,
                  })}
                  onClick={() => (
                    selectedUser.id !== user.id
                      && setSelectedUser(user)
                  )}
                  data-cy="FilterUser"
                  href="#/"
                >
                  {user.name}
                </a>
              ))}
            </p>

            <div className="panel-block">
              <p className="control has-icons-left has-icons-right">
                <input
                  value={query}
                  onChange={event => setQuery(event.currentTarget.value)}
                  data-cy="SearchField"
                  type="text"
                  className="input"
                  placeholder="Search"
                />

                <span className="icon is-left">
                  <i className="fas fa-search" aria-hidden="true" />
                </span>

                {(query.length !== 0) && (
                  <span className="icon is-right">
                    {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
                    <button
                      onClick={() => setQuery('')}
                      data-cy="ClearButton"
                      type="button"
                      className="delete"
                    />
                  </span>
                )}
              </p>
            </div>

            <div className="panel-block is-flex-wrap-wrap">
              <a
                href="#/"
                data-cy="AllCategories"
                className="button is-success mr-6 is-outlined"
              >
                All
              </a>

              {
                categoriesFromServer.map(item => (

                  <a
                    onClick={selectCategory(item)}
                    data-cy="Category"
                    className={classNames(
                      'button mr-2 my-1',
                    )}
                    href="#/"
                  >
                    {item.title}
                  </a>
                ))
              }
            </div>

            <div className="panel-block">
              <a
                onClick={() => {
                  setSelectedUser('All');
                  setQuery('');
                }}
                data-cy="ResetAllButton"
                href="#/"
                className="button is-link is-outlined is-fullwidth"
              >
                Reset all filters
              </a>
            </div>
          </nav>
        </div>

        <div className="box table-container">
          {visibleProduct.length === 0 && (
            <p data-cy="NoMatchingMessage">
              No products matching selected criteria
            </p>
          )}

          {visibleProduct.length > 0 && (
          <table
            data-cy="ProductTable"
            className="table is-striped is-narrow is-fullwidth"
          >
            <thead>
              <tr>
                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    ID

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort" />
                      </span>
                    </a>
                  </span>
                </th>

                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    Product

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort-down" />
                      </span>
                    </a>
                  </span>
                </th>

                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    Category

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort-up" />
                      </span>
                    </a>
                  </span>
                </th>

                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    User

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort" />
                      </span>
                    </a>
                  </span>
                </th>
              </tr>
            </thead>

            <tbody>
              {visibleProduct.map(product => (
                <tr data-cy="Product">
                  <td
                    key={product.id}
                    className="has-text-weight-bold"
                    data-cy="ProductId"
                  >
                    {product.id}
                  </td>

                  <td data-cy="ProductName">{product.name}</td>
                  <td data-cy="ProductCategory">
                    {`${product.category.icon} - ${product.category.title}`}
                  </td>

                  <td
                    data-cy="ProductUser"
                    className={classNames({
                      'has-text-link': product.user.sex === 'm',
                      'has-text-danger': product.user.sex === 'f',
                    })}
                  >
                    {product.user.name}
                  </td>
                </tr>
              ))}

            </tbody>
          </table>
          )}
        </div>
      </div>
    </div>
  );
};
